.clock-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #282c34;
  color: white;
  font-family: Arial, sans-serif;
}

.break-session-container {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

#break-label, #session-label {
  margin-right: 10px;
}

button {
  background-color: #61dafb;
  border: none;
  border-radius: 5px;
  color: #282c34;
  cursor: pointer;
  margin: 0 10px;
  padding: 10px;
}

button:hover {
  background-color: #21a1f1;
}

#time-left {
  font-size: 48px;
  margin: 20px 0;
}

#start_stop, #reset {
  background-color: #61dafb;
  border: none;
  border-radius: 5px;
  color: #282c34;
  cursor: pointer;
  margin: 10px;
  padding: 10px;
}

#start_stop:hover, #reset:hover {
  background-color: #21a1f1;
}
